import "ol/ol.css";
import "ol-ext/dist/ol-ext.css";
//import "ol-layerswitcher/dist/ol-layerswitcher.css";

import Map from "ol/Map";
import OSM from "ol/source/OSM";
import TileLayer from "ol/layer/Tile";
//import ImageLayer from "ol/layer/Image";
import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import KML from "ol/format/KML";
//import LayerSwitcher from "ol-layerswitcher";

import View from "ol/View";
import WMTS from "ol/source/WMTS";
import WMTSTileGrid from "ol/tilegrid/WMTS";
import { get as getProjection } from "ol/proj";
import { getTopLeft, getWidth } from "ol/extent";
//import ImageWMS from "ol/source/ImageWMS";
import LayerSwitcher from "ol-ext/control/LayerSwitcher";
import Scale from "ol-ext/control/Scale";
import JSZip from "jszip";

var projection = getProjection("EPSG:3857");
var projectionExtent = projection.getExtent();
var size = getWidth(projectionExtent) / 256;
var resolutions = new Array(23);
var matrixIds = new Array(23);
for (var z = 0; z < 23; ++z) {
  // generate resolutions and matrixIds arrays for this WMTS
  resolutions[z] = size / Math.pow(2, z);
  matrixIds[z] = z;
}

var view = new View({
  center: [-8153335, 5989747],
  zoom: 7
});

var zip = new JSZip();

function getKMLData(buffer) {
  var kmlData;
  zip.load(buffer);
  var kmlFile = zip.file(/.kml$/i)[0];
  if (kmlFile) {
    kmlData = kmlFile.asText();
  }
  return kmlData;
}

function getKMLImage(href) {
  var url = href;
  var path = window.location.href;
  path = path.slice(0, path.lastIndexOf("/") + 1);
  if (href.indexOf(path) === 0) {
    var regexp = new RegExp(href.replace(path, "") + "$", "i");
    var kmlFile = zip.file(regexp)[0];
    if (kmlFile) {
      url = URL.createObjectURL(new Blob([kmlFile.asArrayBuffer()]));
    }
  }
  return url;
}

var KMZ = /*@__PURE__*/ (function (KML) {
  function KMZ(opt_options) {
    var options = opt_options || {};
    options.iconUrlFunction = getKMLImage;
    KML.call(this, options);
  }

  if (KML) KMZ.__proto__ = KML;
  KMZ.prototype = Object.create(KML && KML.prototype);
  KMZ.prototype.constructor = KMZ;

  KMZ.prototype.getType = function getType() {
    return "arraybuffer";
  };

  KMZ.prototype.readFeature = function readFeature(source, options) {
    var kmlData = getKMLData(source);
    return KML.prototype.readFeature.call(this, kmlData, options);
  };

  KMZ.prototype.readFeatures = function readFeatures(source, options) {
    var kmlData = getKMLData(source);
    return KML.prototype.readFeatures.call(this, kmlData, options);
  };

  return KMZ;
})(KML);

var map = new Map({
  layers: [
    new TileLayer({
      source: new OSM(),
      displayInLayerSwitcher: false
    }),
    new TileLayer({
      minZoom: 13,
      opacity: 1,
      title: "Aerial photography",
      source: new WMTS({
        attributions:
          '<a href="https://servicesmatriciels.mern.gouv.qc.ca/erdas-iws/ogc/wmts/Inventaire_Ecoforestier"' +
          " >Gouvernement du Québec</a>",
        url:
          "https://servicesmatriciels.mern.gouv.qc.ca/erdas-iws/ogc/wmts/Inventaire_Ecoforestier",
        layer: "Inventaire_Ecoforestier",
        matrixSet: "GoogleMapsCompatibleExt2:epsg:3857",
        format: "image/png",
        projection: projection,
        tileGrid: new WMTSTileGrid({
          origin: getTopLeft(projectionExtent),
          resolutions: resolutions,
          matrixIds: matrixIds
        }),
        style: "default",
        wrapX: true
      })
    }),
    new VectorLayer({
      name: "TFN territory",
      source: new VectorSource({
        url: "Timiskaming_consult_dd_Layer.kmz",
        format: new KMZ()
      })
    }),
    new VectorLayer({
      name: "Private land",
      opacity: 0.5,
      source: new VectorSource({
        url: "terres_privees_LayerToKML.kmz",
        format: new KMZ()
      })
    }),
    new VectorLayer({
      name: "Annual cut 2021-22",
      opacity: 0.5,
      source: new VectorSource({
        url: "cut_8251.kmz",
        format: new KMZ()
      })
    })
  ],
  target: "map",
  view
});

var layerSwitcher = new LayerSwitcher({
  // Optional label for button
  groupSelectStyle: "children", // Can be 'children' [default], 'group' or 'none'
  collapsed: false,
  reordering: false,
  activationMode: "click"
});
map.addControl(layerSwitcher);

var scale = new Scale({ editable: false, className: "scale-bar" });
map.addControl(scale);
